import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import BackgroundImage from 'gatsby-background-image'

export default function BlurbPage({ data }) {
	const post = data.markdownRemark
	return (
		<Layout>
			<div>
				<BackgroundImage
					fluid={post.frontmatter.image.childImageSharp.fluid}
					backgroundColor={`#040e18`}
					style={{
						backgroundPosition: 'top center',
						backgroundRepeat: 'none',
						backgroundSize: 'cover',
						minHeight: '430px',
						height: '70vh',
						marginBottom: 60,
						position: 'relative'
					}}
				>
					<div style={{ padding: 20, backgroundColor: 'rgba(224,224,224,0.85)', position: 'absolute', bottom: '10%', right: '5%', borderRadius: 15}}>
						<h1>{post.frontmatter.title}</h1>
        				<p>{post.frontmatter.summary}</p>
					</div>
				</BackgroundImage>
				<div style={{marginTop: 25}} dangerouslySetInnerHTML={{ __html: post.html }} />
			</div>
		</Layout>
	)
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        summary
        date(formatString: "DD MMMM, YYYY")
        image {
          childImageSharp {
            fluid {
				...GatsbyImageSharpFluid
			}
          }
        }
      }
    }
  }
`